body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
h1{
  margin: 0
}
#board, h1, h3{
    text-align: center
}
.restart{
  text-align: center;
  cursor: pointer;
}
.block {
    width: 50px;
    height: 50px;
    background-color: #4291F7;
    padding: 5px;
    text-align: center;
    display: inline-block
}
.block .circle {
    border-radius: 50%;
    background-color: #FFF;
    border: 5px solid #3281F7;
    height: 35px;
    width: 35px;
    display: inline-block;
    margin-top: 2px;
    cursor: pointer;
}
.block .circle.red {
background-color: red;
}
.block .circle.yellow {
background-color: yellow;
}
.players {
  text-align: center;
  margin: 10px 0 10px 0;
}
.player span, .player{
  display: inline-block;
  vertical-align: top;
  font-size: 20px;
}
.player .name{
  margin:5px;
  cursor: pointer;
}
.versus {
    font-size: 30px !important;
    margin: 0px 15px;
}
.case {
    width: 20px;
    height: 20px;
    border-radius: 30px;
    margin: 5px;
}
.case.yellow{
  background-color: yellow
}
.case.red{
  background-color: red
}
.dialogMessage{
  text-align: center;
}
.slideCol{
  width: 25%;
  margin: auto;
}
.boardContent, .slideRow {
    display: inline-block;
    vertical-align: top;
}
.slideRow {
    padding-left: 5px;
}



